.pageItem{display:flex;align-items:center;justify-content:center;width:23px;height:23px;padding:1rem;font-weight:500;margin:0 .5rem;-webkit-user-select:none;-ms-user-select:none;user-select:none}.pageItem.navigate{padding:.3rem;width:30px;height:30px;border-radius:50%;box-shadow:1px 1px 2px #ccc}.pageItem.navigate.next{margin-left:1rem}.pageItem.navigate.prev{margin-right:1rem}.pageItemActive{background-color:#FDD84C;border-radius:4px}
